import React, { useContext, useState } from 'react';
import styles from './styles.module.css';
import { ServiceContext } from '../../../../scripts/context';


const Container = () => {

    const { 
        data, 
        changeHandler,
        errors: { hour, minute },
    } = useContext(ServiceContext);
    
    return (
        <div className={styles.container}>
        
            <div className = {`${hour && 'is-invalid'}`}>
                
                <label for="artistServiceFormDurationHour" className = ""></label>
                
                <select className = "" id="artistServiceFormDurationHour" 
                name = 'hour'
                value = {data.hour}
                onChange = {changeHandler}
                required
                >
                    <option selected disabled value=""></option>
                    <option value = {0}></option>
                    <option value = {1}></option>
                    <option value = {2}></option>
                </select>
                
                {hour && 
                <div className = 'invalid-feedback'>
                        
                </div>
                }

            </div>

            <div className = {`${hour && 'is-invalid'}`}>
                
                <label for="artistServiceFormDurationMinute" className = ""></label>
                
                <select className = "" id="artistServiceFormDurationMinute" 
                name = 'minute'
                value = {data.minute}
                onChange = {changeHandler}
                required
                >
                    <option selected disabled value=""></option>
                    <option value = {0}></option>
                    <option value = {15}></option>
                    <option value = {30}></option>
                    <option value = {45}></option>
                </select>
                
                {hour && 
                <div className = 'invalid-feedback'>
                        
                </div>
                }

            </div>

        </div>
    );
};

export default Container;